.make-page-container{
    display:flex;
    flex-direction:row;
    height: 100%;
    width:100%;
}

.make-page-example{
    width:65%;
    height:100%;
    background-color:pink;
}
.make-page-make-space{
    position:fixed;
    right:0;
    width:35%;
    height:100%;
    background-color:red;
    flex-direction: column;
    display: flex;
}

.section-selector-container{
    display: flex;
    flex-direction:row;
}

.make-page-section-selector{
    width:100%;
    border:1px solid black;
    background-color:white;
    cursor:pointer;
}

.section-image{
    width:200px;
    height:200px;
}