.main-page-container{
    height:100%;
    width:100%;
    background-color:red;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.main-page-section{
    width:100%;
    display: flex;
    flex-direction: row;
    height:300px;
    border: 1px solid black;
    justify-content : center;
    align-items : center;
}

.main-section-left{
    width:100%;
    height: 100%;
    display:flex;
    background-color:green;
    justify-content : center;
    align-items : center;
}
.main-section-right{
    width:100%;
    height: 100%;
    display:flex;
    background-color:blue;
    justify-content : center;
    align-items : center;
}